import React from 'react';

import bookCover from '../../assets/images/book_cover.jpg';
import './Book.css';

const book = (props) => (
    <section id="book" className="Book" >
        <h2></h2>
        <div className="BookContainer">
            <img alt="Book Cover" src={bookCover} />
            <div className="BookInfo">
                <p>Taste Weddings  Jessica Kong </p>
                <p>
                Jessica
                </p>
                <h3></h3>
                <p>
                  <br />
                  /  Jessica Kong<br />
                 	HK$ 88.0 <br />
                 	176  <br />
                   	2015-06 <br />
                ISBN9789626788677 <br />
                </p>
               
                <a className="btn" href="http://www.etpress.com.hk/etpress/bookdetail.do?id=9789626788677"></a>
            </div>
            <div className="clear"></div>
        </div>
        
    </section>
)

export default book;


