import React from 'react';

import NavigationItem from './NavigationItem/NavigationItem';
import './NavigationItems.css';

const navigationItems = (props) => {
    return (
        <ul className="NavItems">
          <NavigationItem link="service" clickLink={props.clickHandler}></NavigationItem>
          <NavigationItem link="photo" clickLink={props.clickHandler}></NavigationItem>
          <NavigationItem link="book" clickLink={props.clickHandler}></NavigationItem>
          <NavigationItem link="about" clickLink={props.clickHandler}></NavigationItem>
          <NavigationItem link="contact" clickLink={props.clickHandler}></NavigationItem>
        </ul>
    )
}

export default navigationItems;


